/* ==========
APP HEADER
========== */

.app-header {
    min-height: 6.4rem;
    background-color: #fff;
    padding: 2rem 4rem;
}

.app-name {
    font-size: 1.6rem;
    text-transform: uppercase;
    font-weight: 300;
    letter-spacing: 1rem;
    line-height: 1.6;
    text-align: center;
    padding: 0;
    margin: 0;

    a:hover {
        color: #186ddb;
    }

    span:first-child {
        &:after {
            content: '//';
            margin: 0 2.4rem;
        }
    }
}


// RESPONSIVE
@media screen and (max-width: 960px) {

    .app-name {
        text-align: left;
    }

}

@media screen and (max-width: 860px) {

    .app-name {
        span {
            display: block;
        }

        span:first-child {
            &:after {
                content: unset;
            }
        }
    }

}

@media screen and (max-width: 680px) {

    .app-header {
        padding-left: 1.6rem;
        padding-right: 1.6rem;
    }

}

@media screen and (max-width: 420px) {

    .app-name {
        letter-spacing: .6rem;
    }

}