/* ==========
VARIABLES AND MIXINS
========== */

// Main:
$cyan: cyan;


// Greys:
$grey-1: #333;


// Typography
@mixin smooth {
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}


// Layout
@mixin flex-column {
    display: flex;
    flex-direction: column;
}

@mixin flex-row {
    display: flex;
    flex-direction: row;
}


// Transitions
@mixin transition02 {
    transition: all .2s ease-out;
}


// Shadows
@mixin shadow01 {
    box-shadow: 0.2rem 0.2rem 0.8rem 0 rgba(00, 00, 00, 0.2);
}