.icon-scroll-down {
    position: absolute;
    top: auto;
    bottom: 1rem;

    width: 1.8rem;
    height: 3.6rem;
    border: 2px solid rgba(255, 255, 255, .2);
    border-radius: 1rem;
    display: block;

    &::before {
        content: '';
        width: .6rem;
        height: .6rem;
        position: absolute;
        top: .8rem;
        left: 50%;
        transform: translateX(-50%);
        background-color: rgba(255, 255, 255, .6);
        border-radius: 50%;
        opacity: 1;
        animation: wheel 4s infinite;
        -webkit-animation: wheel 4s infinite;
    }
}

@keyframes wheel {
    to {
        opacity: 0;
        top: 2.6rem;
    }
}

@-webkit-keyframes wheel {
    to {
        opacity: 0;
        top: 2.6rem;
    }
}

// RESPONSIVE
@media screen and (max-width: 860px) {

    .icon-scroll-down {
        display: none;
    }


}