/* ==========
LANDING PAGE CARD TEMPLATE
========== */

// Landing intro slide
.card-intro-landing {
  @include flex-column;
  justify-content: center;
  align-items: center;

  width: calc(100vw - 16rem);
  height: calc(100vh - 12rem);
  margin: 0 8rem 8rem 8rem;
  padding: 8rem 0;

  // background-color: $primary-1;
  background-color: #171b31;
  position: relative;

  .folio-intro {
    color: #fff;
    width: 100%;
    max-width: 76rem;
  }

  .text {
    width: 100%;
    max-width: 64rem;
    margin: auto;
  }

  p:first-child {
    font-size: 2.4rem;
    line-height: 1.6;
  }

  p {
    font-size: 1.6rem;
    line-height: 1.6;
    margin-bottom: 2rem;
  }

}

.folio-intro-title {
  font-size: 1.6rem;
  text-transform: uppercase;
  font-weight: 300;
  letter-spacing: 1rem;
  text-align: center;

  span:first-child {
    &:after {
      content: '//';
      margin: 0 2.4rem;
    }
  }
}


// RESPONSIVE
@media screen and (max-height: 800px) {

  .card-intro-landing {
    height: auto;
  }
}

@media screen and (max-width: 1400px) {

  .card-intro-landing {
    width: calc(100vw - 8rem);
    margin: 0 4rem 4rem 4rem;
  }
}

@media screen and (max-width: 960px) {

  .card-intro-landing {
    height: auto;

    .folio-intro {
      @include flex-column;
      align-items: center;
      max-width: unset;
    }

    p:first-child {
      font-size: 2rem;
    }
  }

}

@media screen and (max-width: 860px) {

  .folio-intro-title {
    line-height: 2;

    span {
      display: block;
      width: 100%;
    }

    span:first-child {
      &:after {
        content: '';
        margin: 0;
      }
    }
  }

  .card-intro-landing {
    width: calc(100vw - 8rem);
    margin: 0 4rem 4rem 4rem;
    padding: 8rem 0;

    .text {
      width: 100%;
      max-width: unset;
      padding: 0 4rem;
    }
  }

}

// close 860 media-query

@media screen and (max-width: 680px) {

  .card-intro-landing {
    width: calc(100vw - 3.2rem);
    margin: 0 1.6rem 4rem 1.6rem;

    .content {
      padding-top: 2.4rem;
    }
  }

}

@media screen and (max-width: 375px) {

  .card-intro-landing {
    .text {
      padding: 0 2.4rem;
    }

    .icon-scroll-down {
      display: none;
    }
  }

}