/* ==========
TYPOGRAPHY
========== */

html {
  font-size: 62.5%;
  font-weight: 400;
  font-style: normal;
  padding: 0;
  margin: 0;
}

body {
  font-family: "Helvetica Neue", Helvetica, Arial, sans-serif;
  // color: $primary-1;
  color: #171b31;
  font-size: 1.4rem;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  padding: 0;
  margin: 0;
}

h1,
h2,
h3,
h4 {
  font-family: "Helvetica Neue";
  font-weight: 600;
  font-style: normal;
}

h1 {
  font-size: 8rem;
}

h2 {
  font-size: 6rem;
}

h3 {
  font-size: 1.6rem;
}

h4 {
  font-size: 1.4rem;
}

p {
  font-size: 1.4rem;
  margin-bottom: 2.4rem;
}

a {
  // color: $primary-1;
  color: #171b31;
  position: relative;

  &:hover {
    text-decoration: none;
    color: inherit;
  }
}

.link-inline {
  display: inline-block;
  position: relative;

  &:before {
    position: absolute;
    left: 0;
    bottom: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(252, 252, 2, .5);
    transform-origin: bottom;
    transform: scaleY(.1);
    @include transition02;
    content: '';
  }

  &:hover:before {
    background-color: rgba(252, 252, 2, .2);
    transform: scaleY(1);
  }
}

// h2 level
.folio-view-title {
  margin-top: 1.6rem;
  margin-bottom: 1.6rem;
  font-weight: normal;
}

// h3 level
.folio-section-title {
  margin-bottom: 2rem;
}

// h4 level
.folio-component-title {
  font-size: 1.8rem;
  font-weight: normal;
  line-height: 1.8;
  margin-bottom: 0.8rem;

  small {}
}

// h5 level
.folio-block-title {
  @extend .folio-component-title;
  font-size: 1.4rem;
}

// App header title
.app-name {
  font-size: 1.6rem;
  text-transform: uppercase;
  font-weight: 300;
  letter-spacing: 1rem;
  text-align: center;

  span:first-child {
    &:after {
      content: '//';
      margin: 0 2.4rem;
    }
  }
}