/* ==========
CASE STUDY IEP Intro
========== */

.card-iep-intro {
  @include flex-column;
  justify-content: center;

  width: calc(100vw - 16rem);
  height: calc(100vh - 12rem);
  margin: 0 8rem 4rem 8rem;
  padding: 4rem 8rem;
  color: #fff;
  background: #000;
  position: relative;

  .content-row {
    @include flex-row;
    justify-content: space-between;
    align-items: center;
    width: 100%;
  }

  .content {
    width: 100%;
    max-width: 60rem;
    padding: 4rem 8rem 4rem 0;
    position: relative;

    &:before {
      content: "";
      position: absolute;
      top: 0;
      left: 0;
      height: 0.8rem;
      width: 7.2rem;
      background-color: #47c8bc;
    }
  }

  .title {
    font-size: 4rem;
    line-height: 1.1;
    margin-top: 0;
    margin-bottom: 3.2rem;
  }

  .para {
    font-size: 1.8rem;
    line-height: 1.4;
  }

  .link {
    &:after {
      background-color: #47c8bc;
    }

    &:hover {
      .fa-angle-right {
        color: #47c8bc;
      }
    }
  }

  .link-inline {
    color: #fff;

    &:before {
      background-color: rgba(71, 200, 188, .4);
    }
  }

  .btn-row {
    margin-top: 4rem;
  }

  .logos {
    position: absolute;
    top: 0;
    right: 0;
    padding: 4rem 6rem;
    @include flex-row;
    justify-content: space-between;
    width: 100%;
    max-width: 52rem;

    .logoAnthem {
      height: 4rem;
      width: auto;
    }

    .logoIngenio {
      height: 3.6rem;
      width: auto;
      margin-top: 1rem;
    }
  }

  .pullquote {
    color: #444;
    font-size: 3rem;
    line-height: 1.3;
    font-weight: 700;
    width: 100%;
    max-width: 44rem;
  }

  .icon-scroll-down {
    left: 50%;
  }
}


// RESPONSIVE
@media screen and (max-height: 800px) {

  .card-iep-intro {
    height: auto;
  }
}

@media screen and (max-width: 1400px) {

  .card-iep-intro {
    width: calc(100vw - 8rem);
    margin: 0 4rem 4rem 4rem;
    padding: 4rem;
  }
}

@media screen and (max-width: 1100px) {

  .card-iep-intro {
    .logos {
      padding: 4rem;
      max-width: 36rem;

      .logoAnthem {
        height: 2.8rem;
        width: auto;
      }

      .logoIngenio {
        height: 2.4rem;
        width: auto;
        margin-top: 1rem;
      }
    }

    .pullquote {
      font-size: 2.8rem;
      max-width: 36rem;
    }
  }
}

@media screen and (max-width: 960px) {

  .card-iep-intro {
    height: auto;

    .content-row {
      flex-direction: column;
      align-items: center;
    }

    .content {
      max-width: 60rem;
      margin-top: 8rem;
      padding-right: 0;
    }

    .pullquote {
      max-width: 48rem;
      margin-bottom: 4rem;
    }
  }
}

@media screen and (max-width: 680px) {

  .card-iep-intro {
    width: calc(100vw - 3.2rem);
    margin: 0 1.6rem 4rem 1.6rem;
  }
}

@media screen and (max-width: 375px) {

  .card-iep-intro {
    padding-right: 2.4rem;
    padding-left: 2.4rem;

  }

}