/* ==========
CASE STUDY IEP Prototyping card
========== */

.card-iep-prototyping {
    @include flex-column;
    justify-content: center;

    width: calc(100vw - 16rem);
    height: calc(100vh - 8rem);
    margin: 4rem 8rem;
    padding: 4rem 8rem;
    background: #F7F7F7;
    position: relative;

    .content {
        width: 100%;
        max-width: 48rem;
    }

    .title {
        max-width: 60rem;
        color: #000dd0;
        font-size: 4rem;
        line-height: 1.1;
        margin-top: 3.2rem;
        margin-bottom: 2.4rem;
        position: relative;

        &:before {
            content: "";
            position: absolute;
            top: -3.2rem;
            left: 0;
            height: 0.8rem;
            width: 7.2rem;
            background-color: #808080;
        }
    }

    .para {
        font-size: 1.8rem;
        line-height: 1.4;
    }

    .image {
        position: absolute;
        top: 0;
        right: 0;
        bottom: 0;
        width: 40%;
        overflow: hidden;

        .svg-mask {
            position: absolute;
            top: 0;
            left: 0;
            bottom: 0;
            height: 100vh;
            width: auto;
        }

        img {
            width: auto;
            height: 100vh;
        }
    }

}